"use client";

import { useEffect, useRef, useState } from "react";
import { motion } from "framer-motion";

interface PricingSectionProps {
  option1Name: string;
  option1Price: number;
  option1Desc: string;
  option2Name: string;
  option2Price: number;
  option2Desc: string;
  onPricingViewed?: () => void;
}

const sharedFeatures = [
  "All included services",
  "Dedicated account manager",
  "Monthly performance reports",
  "Priority support",
  "Strategy sessions",
];

export default function PricingSection({
  option1Name,
  option1Price,
  option1Desc,
  option2Name,
  option2Price,
  option2Desc,
  onPricingViewed,
}: PricingSectionProps) {
  const ref = useRef<HTMLDivElement>(null);
  const [hasTracked, setHasTracked] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && !hasTracked) {
          setHasTracked(true);
          onPricingViewed?.();
        }
      },
      { threshold: 0.3 }
    );

    if (ref.current) observer.observe(ref.current);
    return () => observer.disconnect();
  }, [hasTracked, onPricingViewed]);

  return (
    <section id="pricing" className="py-24 px-6" ref={ref}>
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.5 }}
          className="text-center mb-16"
        >
          <span className="text-accent-purple text-sm font-medium uppercase tracking-widest">
            Investment
          </span>
          <h2 className="font-clash text-4xl md:text-5xl font-bold mt-3">
            Partnership Options
          </h2>
        </motion.div>

        {/* Cards */}
        <div className="grid md:grid-cols-2 gap-6">
          {/* Option 1 — Recommended */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
            whileHover={{ y: -6 }}
            className="relative rounded-2xl p-[1px] bg-gradient-to-br from-accent-purple to-accent-pink overflow-hidden"
          >
            <div className="ribbon">Recommended</div>
            <div className="relative rounded-2xl bg-dark-card p-8 h-full">
              <div className="mb-6">
                <h3 className="font-clash text-xl font-semibold mb-1">
                  {option1Name}
                </h3>
                <p className="text-dark-text text-sm">{option1Desc}</p>
              </div>
              <div className="mb-8">
                <span className="font-clash text-5xl font-bold text-accent-purple">
                  ${option1Price.toLocaleString()}
                </span>
                <span className="text-dark-text text-sm">/month</span>
              </div>
              <ul className="space-y-3">
                {sharedFeatures.map((feature) => (
                  <li
                    key={feature}
                    className="flex items-center gap-2 text-sm text-dark-text-light"
                  >
                    <svg
                      className="w-4 h-4 text-accent-purple shrink-0"
                      fill="none"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      strokeWidth="2"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M5 13l4 4L19 7"
                      />
                    </svg>
                    {feature}
                  </li>
                ))}
              </ul>
            </div>
          </motion.div>

          {/* Option 2 */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5, delay: 0.1 }}
            whileHover={{ y: -6 }}
            className="rounded-2xl border border-dark-border bg-dark-card p-8"
          >
            <div className="mb-6">
              <h3 className="font-clash text-xl font-semibold mb-1">
                {option2Name}
              </h3>
              <p className="text-dark-text text-sm">{option2Desc}</p>
            </div>
            <div className="mb-8">
              <span className="font-clash text-5xl font-bold">
                ${option2Price.toLocaleString()}
              </span>
              <span className="text-dark-text text-sm">/month</span>
            </div>
            <ul className="space-y-3">
              {sharedFeatures.map((feature) => (
                <li
                  key={feature}
                  className="flex items-center gap-2 text-sm text-dark-text-light"
                >
                  <svg
                    className="w-4 h-4 text-dark-text shrink-0"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    strokeWidth="2"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M5 13l4 4L19 7"
                    />
                  </svg>
                  {feature}
                </li>
              ))}
            </ul>
          </motion.div>
        </div>
      </div>
    </section>
  );
}
