"use client";

import { motion, type Variants } from "framer-motion";
import ScrollIndicator from "./ScrollIndicator";

interface HeroSectionProps {
  clientName: string;
  heroTitle: string;
  heroSubtitle: string | null;
  customNote: string | null;
}

const stagger: Variants = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: { staggerChildren: 0.15 },
  },
};

const slideUp: Variants = {
  hidden: { opacity: 0, y: 30 },
  show: {
    opacity: 1,
    y: 0,
    transition: { duration: 0.6, ease: [0.25, 0.4, 0.25, 1] as [number, number, number, number] },
  },
};

export default function HeroSection({
  clientName,
  heroTitle,
  heroSubtitle,
  customNote,
}: HeroSectionProps) {
  // Split hero title to animate "Marketing" with gradient
  const titleParts = heroTitle.split("Marketing");
  const hasMarketingWord = titleParts.length > 1;

  const scrollToSection = (id: string) => {
    document.getElementById(id)?.scrollIntoView({ behavior: "smooth" });
  };

  return (
    <section className="relative min-h-screen flex items-center justify-center px-6">
      <motion.div
        variants={stagger}
        initial="hidden"
        animate="show"
        className="max-w-4xl mx-auto text-center"
      >
        {/* Badge */}
        <motion.div variants={slideUp} className="mb-8">
          <span className="inline-flex items-center gap-2 px-4 py-2 rounded-full border border-accent-pink/30 bg-accent-pink/10 text-accent-pink text-sm font-medium">
            <span className="w-1.5 h-1.5 rounded-full bg-accent-pink" />
            Prepared for {clientName}
          </span>
        </motion.div>

        {/* Title */}
        <motion.h1
          variants={slideUp}
          className="font-clash text-5xl sm:text-6xl md:text-7xl lg:text-8xl font-bold leading-[1.1] mb-6"
        >
          {hasMarketingWord ? (
            <>
              {titleParts[0]}
              <span className="gradient-text">Marketing</span>
              {titleParts[1]}
            </>
          ) : (
            heroTitle
          )}
        </motion.h1>

        {/* Subtitle */}
        <motion.p
          variants={slideUp}
          className="text-dark-text-light text-lg md:text-xl max-w-2xl mx-auto mb-8 leading-relaxed"
        >
          {heroSubtitle ||
            "A comprehensive digital marketing partnership designed to elevate your brand, drive growth, and deliver measurable results."}
        </motion.p>

        {/* Custom note */}
        {customNote && (
          <motion.div
            variants={slideUp}
            className="max-w-lg mx-auto mb-8 p-4 rounded-xl border border-dark-border bg-dark-card/50 text-dark-text-light text-sm italic"
          >
            &ldquo;{customNote}&rdquo;
          </motion.div>
        )}

        {/* CTAs */}
        <motion.div
          variants={slideUp}
          className="flex flex-col sm:flex-row items-center justify-center gap-4"
        >
          <button
            onClick={() => scrollToSection("services")}
            className="btn-shimmer px-8 py-3.5 rounded-xl bg-gradient-to-r from-accent-pink to-accent-purple text-white font-semibold text-sm transition-transform hover:scale-[1.02] active:scale-[0.98]"
          >
            Explore Services ↓
          </button>
          <button
            onClick={() => scrollToSection("pricing")}
            className="px-8 py-3.5 rounded-xl border border-dark-border text-white font-semibold text-sm hover:bg-white/5 transition-colors"
          >
            View Pricing
          </button>
        </motion.div>
      </motion.div>

      <ScrollIndicator />
    </section>
  );
}
