"use client";

import { motion } from "framer-motion";

interface ServiceCardProps {
  icon: string;
  title: string;
  subtitle: string | null;
  summary: string;
  color: string;
  isSelected: boolean;
  onClick: () => void;
}

export default function ServiceCard({
  icon,
  title,
  subtitle,
  summary,
  color,
  isSelected,
  onClick,
}: ServiceCardProps) {
  return (
    <motion.div
      onClick={onClick}
      whileHover={{ scale: 1.04 }}
      whileTap={{ scale: 0.98 }}
      className={`cursor-pointer p-6 rounded-2xl border transition-colors duration-300 ${
        isSelected
          ? "bg-dark-card border-opacity-50"
          : "bg-dark-card/30 border-dark-border hover:border-opacity-40"
      }`}
      style={{
        borderColor: isSelected ? color : undefined,
        boxShadow: isSelected ? `0 0 30px ${color}15` : undefined,
      }}
    >
      <div className="text-3xl mb-4">{icon}</div>
      <h3 className="font-clash text-lg font-semibold mb-1">{title}</h3>
      {subtitle && (
        <p className="text-xs text-dark-text mb-2">{subtitle}</p>
      )}
      <p className="text-dark-text-light text-sm leading-relaxed line-clamp-3">
        {summary}
      </p>
      <div className="mt-4 flex items-center gap-1.5">
        <div
          className="w-1.5 h-1.5 rounded-full"
          style={{ backgroundColor: color }}
        />
        <span className="text-xs" style={{ color }}>
          {isSelected ? "Selected" : "Learn more"}
        </span>
      </div>
    </motion.div>
  );
}
