"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

interface CTASectionProps {
  proposalId: string;
  status: string;
  onAccept: () => Promise<void>;
}

export default function CTASection({
  proposalId,
  status,
  onAccept,
}: CTASectionProps) {
  const [showModal, setShowModal] = useState(false);
  const [isAccepting, setIsAccepting] = useState(false);
  const [accepted, setAccepted] = useState(status === "accepted");

  const handleAccept = async () => {
    setIsAccepting(true);
    try {
      await onAccept();
      setAccepted(true);
      setShowModal(false);
    } catch {
      alert("Something went wrong. Please try again.");
    } finally {
      setIsAccepting(false);
    }
  };

  return (
    <>
      <section className="py-24 px-6">
        <div className="max-w-3xl mx-auto">
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            whileInView={{ opacity: 1, y: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.5 }}
            className="rounded-3xl p-12 text-center bg-gradient-to-br from-accent-pink/20 via-accent-purple/10 to-accent-green/10 border border-dark-border"
          >
            <h2 className="font-clash text-3xl md:text-4xl font-bold mb-4">
              {accepted ? "Proposal Accepted!" : "Ready to Grow Together?"}
            </h2>
            <p className="text-dark-text-light mb-8 max-w-lg mx-auto">
              {accepted
                ? "Thank you for choosing 2FLY Marketing. We're excited to start this partnership and drive results for your business."
                : "Let's turn your vision into reality. Accept this proposal to get started, or schedule a call to discuss further."}
            </p>

            {!accepted && (
              <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
                <button
                  onClick={() => setShowModal(true)}
                  className="btn-shimmer px-8 py-3.5 rounded-xl bg-gradient-to-r from-accent-pink to-accent-purple text-white font-semibold text-sm transition-transform hover:scale-[1.02] active:scale-[0.98]"
                >
                  Accept Proposal
                </button>
                <a
                  href="https://calendly.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="px-8 py-3.5 rounded-xl border border-dark-border text-white font-semibold text-sm hover:bg-white/5 transition-colors"
                >
                  Schedule a Call
                </a>
              </div>
            )}

            {accepted && (
              <div className="inline-flex items-center gap-2 px-6 py-3 rounded-xl bg-accent-green/10 border border-accent-green/30 text-accent-green">
                <svg
                  className="w-5 h-5"
                  fill="none"
                  viewBox="0 0 24 24"
                  stroke="currentColor"
                  strokeWidth="2"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M5 13l4 4L19 7"
                  />
                </svg>
                Proposal Accepted
              </div>
            )}
          </motion.div>
        </div>
      </section>

      {/* Confirmation Modal */}
      <AnimatePresence>
        {showModal && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="fixed inset-0 z-[100] flex items-center justify-center px-4 bg-black/60 backdrop-blur-sm"
            onClick={() => !isAccepting && setShowModal(false)}
          >
            <motion.div
              initial={{ opacity: 0, scale: 0.95 }}
              animate={{ opacity: 1, scale: 1 }}
              exit={{ opacity: 0, scale: 0.95 }}
              transition={{ type: "spring", stiffness: 400, damping: 30 }}
              onClick={(e) => e.stopPropagation()}
              className="bg-dark-card border border-dark-border rounded-2xl p-8 max-w-md w-full"
            >
              <h3 className="font-clash text-2xl font-bold mb-2">
                Accept Proposal?
              </h3>
              <p className="text-dark-text-light text-sm mb-6">
                By accepting this proposal, you confirm your interest in
                partnering with 2FLY Marketing. Our team will reach out to
                finalize the details and get started.
              </p>
              <div className="flex gap-3">
                <button
                  onClick={handleAccept}
                  disabled={isAccepting}
                  className="flex-1 btn-shimmer px-6 py-3 rounded-xl bg-gradient-to-r from-accent-pink to-accent-purple text-white font-semibold text-sm disabled:opacity-50"
                >
                  {isAccepting ? "Accepting..." : "Yes, Accept"}
                </button>
                <button
                  onClick={() => setShowModal(false)}
                  disabled={isAccepting}
                  className="flex-1 px-6 py-3 rounded-xl border border-dark-border text-white font-semibold text-sm hover:bg-white/5 transition-colors disabled:opacity-50"
                >
                  Cancel
                </button>
              </div>
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}
